@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', sans-serif;
}
:root{
  --primary-color: orange
}
.primary-button{
    padding: 8px 15px;
    background: orange;
    background: var(--primary-color);
    color: white;
    font-weight: 600;
}
.secondary-button{
  padding: 8px 15px;
  background: white;
  color: orange;
  color: var(--primary-color);
  font-weight: 600;
  border: 2px solid orange;
  border: 2px solid var(--primary-color);
}
button{
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
  letter-spacing: 1.2px;
}
.App{
  padding: 30px 100px;
}

@media (max-width: 800px){
  .App{
    padding: 30px;
  }
}
header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    display: flex;
    align-items: center;
    margin-right: 50px;
}
header ul{
    display: flex;
    align-items: center;
}
header ul li{
    list-style: none;
    margin-right: 30px;
    font-weight: 700;
    text-decoration: none;
}
header ul li a{ 
    text-decoration: none;
    color: rgb(75, 75, 75);
}

@media (max-width: 800px){
    header ul{
        display: none
    }
}
.home{
    margin-top: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.home_content{
    width: 40%;
}
.home_content p{
    font-weight: 600;
}
.home .title{
    font-size: 50px;
    margin: 30px 0;
}
.home .title span{
    color: var(--primary-color);
 }
.home img{
    width: 50%;
}
.home .buttons{
    margin: 30px 0;
}
.home .buttons button{ 
    margin-right: 30px;
}

@media (max-width: 800px){
    .home{
        margin-top: 100px;
        flex-direction: column-reverse;
    }
    .home .title{
        font-size: 40px;
    }
    .home_content{
        width: 100%;
        margin-top: 100px;
    }
    .home img{
        width: 100%;
    }
}
